import React from 'react';
import '../../../styles/flow.css';
import '../../market/container/datatables.css';
import {listAccount,updatePrimaryAccount} from '../../../actions/account_actions';
import FlowContainer from '../../common/container/core/FlowContainer';
import {dd_value} from '../../../actions/common_actions';
import {AccountTransaction} from '../component/AccountTransaction';
import StatusButton from '../../common/component/core/StatusButton';
import {Homeacc_prvdrLogoselector, lbl_amt, multi_acc_pur_tostring} from '../../../helpers/common_helper';
import {check_priv, get} from '../../../helpers/storage_helper';
import ReactTable from 'react-table';

import $ from 'jquery';
class ListAccount extends FlowContainer {

  state = {
     accountList: null  ,
     editScreen: false,     
     row_data:[]
  }


   updatePrimaryAccount = (event) => {
    const is_primary_acc = true;
      var request = {};
      if(this.props.entity === "customer"){
        const cust_id = this.props.entity_id;  
        request = {"id":event.target.id,cust_id,"is_primary_acc":is_primary_acc};
      }else if(this.props.entity === "lender"){
         const lender_code = this.props.entity_id;
         request = {"id":event.target.id,lender_code,"is_primary_acc":is_primary_acc};

      }
    const request_data = {"account":request}
      updatePrimaryAccount(this.req(request_data))
         .then((response) => {
          if(!response){return };
              if(response.status === "success"){
                window.location.reload();
              }else{
                alert(response.message + "\n" + JSON.stringify(response.data));  
              }
            }
          ); 

        }
  makePrimaryButton(account_id, is_primary_acc, status){
    if(is_primary_acc == 0 && status === "enabled"){
      return <input id={account_id} type='button' className='btn btn-primary' onClick={this.updatePrimaryAccount} value="Make Primary A/C " />
    }else{
      return null;
    }
  }

  handleEditAccount = (event) => {
    //this.setState({editScreen: true, account_id: event.target.id});
    this.props.onEditClick(event);

  }
  handleAccTxns = (event) => {
    const acc_id = event.target.id;
    this.entity_id = this.props.entity_id;    
    this.entity = this.props.entity;    
    window.open("/acc_txns/"+this.entity+"/"+this.entity_id+"/"+acc_id,"_blank");
  }

  componentWillMount(){
      const country_code = this.country_code;
      this.account = {}
      this.table_id = this.props.entity_id;  
      this.account = {country_code};      
      if(this.props.entity == "customer"){
        this.account.cust_id = this.props.entity_id;  
        this.list_accounts(this.account);
      }else if(this.props.entity == "lender"){
        this.account.lender_code = this.props.entity_id;
        this.setOptionsToStateFromApi("acc_prvdr");
      }else if(this.props.entity == "acc_prvdr"){
         this.account.acc_prvdr_code = this.props.entity_id;
         this.list_accounts(this.account);
      }

      if(check_priv(this.props.entity , 'view_acc_txn')){
          this.view_txn_btn_style = "show_div"
      }else{
          this.view_txn_btn_style = "disable_div"
      }

     if(check_priv(this.props.entity , 'create_account')){
          this.configure_btn_style = "show_div"
     }else{
          this.configure_btn_style = "disable_div"
     }
  }  

  list_accounts = (account) => {
      listAccount(this.req({account}))
        .then((response) => {
          if(!response){return };
          this.setState(state => state.row_data = response.data )
        }); 
        let accountList = this.getListColumns() 
        this.setState({accountList})           
  }

  getListColumns = () => {
    const is_super_user = ['super_admin', 'app_support'].includes(get('role_codes'))
    return [
      {
        Header : "ACC PRVDR",
        id : "acc_prvdr_code",
        accessor : row => <Homeacc_prvdrLogoselector logotype={row.acc_prvdr_code}/>
      },
      {
        Header : "A/C Number",
        accessor : "acc_number"
      },
      {
        Header : "Account Purpose",
        id : "acc_purpose", 
        accessor: row => <div>{multi_acc_pur_tostring(row.acc_purpose)}</div>
      },
      {
        Header : () =>(<div className={'tableAmount_rightAlign'}>Balance</div>),
        id : "balance",
        accessor: row => <div className={'tableAmount_rightAlign'}>{lbl_amt(row.balance, this.currency_code)}</div>
      },
      {
        Header : () =>(<div>Status</div>),
        id : "status",
        accessor: row => <div className={'tableAmount_centerAlign'}><StatusButton className='text-center' head='1' id={row.id} parent={this.props.entity} entity="accounts" status={row.status} btn_disabled={row.status === 'disabled' && !is_super_user}/></div>
      },
      {
        Header : () =>(<div>Action</div>),
        id : "Action",
        accessor: row => <StatusButton head='2' id={row.id} parent={this.props.entity} entity="accounts" status={row.status} btn_disabled={row.status === 'disabled' && !is_super_user}/>
      },
      {
        Header : "Action",
        id : "action",
        accessor: row => <input id={row.id} type="button" value="Configure" onClick={this.handleEditAccount} className={`btn btn-primary btn-std ${this.configure_btn_style}`}/>
      },
      {
        Header : "Action",
        id : "2",
        accessor: row => <input id={row.id} type="button" value="View Txns" onClick={this.handleAccTxns} className={`btn btn-primary btn-std ${this.view_txn_btn_style}`}/>
      },
      {
        Header : "Action",
        show : this.props.entity == "lender" ? true : false,
        id : "3",
        accessor: row => 
          <AccountTransaction selected_acc_prvdr_code={this.selected_acc_prvdr_code} account={row} id="acc_txn" entity={this.props.entity} entity_id={this.props.entity_id} />        
      },      
    ]
  }


  changeDataPrvdr = (event) => {
    var table = $('#table2').DataTable();
    table.destroy();
    this.selected_acc_prvdr_code = event.target.value;
    this.account.lender_acc_prvdr_code = event.target.value;
    this.list_accounts(this.account);
  }

  render(){
    const NullComponent = () => null;
    return( 
        <>
          {this.props.entity == "lender" &&
          <div className='row text-labelblue'>
          <div className="col-md-3">
                <span>Account Provider</span><font className="text-danger">   *  </font>
                <select id="lender_acc_prvdr_code" onChange={this.changeDataPrvdr}  className="form-control" type="text" required="required">
                   {this.state.acc_prvdr_dd}
                </select>
          </div>
          </div>
        }       
                {this.state.accountList != null &&
                <>
                      <div className="table-responsive">
                        <ReactTable className='rt_borderBottom' NoDataComponent={NullComponent} showPaginationBottom={false} minRows = {1}  data={this.state.row_data}  
                          columns={this.state.accountList} 
                          resizable={false}  defaultSorted={[{id: "acc_prvdr_code",
                          desc: true}]}  />
                      </div>
                </>
                }  

                {this.state.accountList == null &&  
                  <div><p>No Accounts Available ! </p></div>
                }
      
      </>
      );      
 } 
 
}


export default ListAccount;
