import React,{useState, useEffect} from 'react'
import { GoogleMap, useLoadScript, Marker, InfoWindow } from '@react-google-maps/api';

const containerstyle = {
    width: "100%",
    height:'80vh'
}

export default function GoogleMapSetup ({coordinates}) { 
    
    const [postioningVal, setpostioningVal] = useState({
        lat:8.088306,
        lng:77.538452
    })
 
    

    useEffect(() => { 
        if(coordinates){
            const latstr = coordinates.split(',')[0];            
            const lngstr = coordinates.split(',')[1];
            const lng = Number(lngstr);
            const lat = Number(latstr);            
            setpostioningVal({
                    "lat":lat,
                    "lng":lng,                
            })
        }                                                                               
    }, [coordinates])      
    
    const markerHandler = (val) => {
        console.log(val,"value from marker")
    }    

    const { isLoaded, loadError } = useLoadScript({
         googleMapsApiKey:"AIzaSyBXK88JmBu1jfJinv0d8bU4sFZN8o7LgPI"   
    });

    if (loadError) return "Error loading maps";
    if(!isLoaded) return "Loading maps";

    return(  
        <div>
            <GoogleMap 
                mapContainerStyle={containerstyle}
                zoom={20}
                center={postioningVal}                               
            > 
                <Marker position={{lat:postioningVal.lat,lng:postioningVal.lng}} onClick={(val)=>markerHandler(val)}/> 
            </GoogleMap>          
        </div>      
    )
}