import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {Link} from "react-router-dom";
import ReactTable from "react-table"; 
import {Button} from 'react-bootstrap';
import {listWriteOff} from '../../actions/write_off_action';
import {dd_value} from "../../actions/common_actions";
import {lbl_amt} from '../../helpers/common_helper';
import $ from 'jquery';

class WriteOffList extends FlowComponent {
    state = {
		columns :[],
		list : []
	}

    componentWillMount(){
        const write_off = {write_off_status : "requested"};
        listWriteOff(this.req(write_off, true))
        .then((response) => {
            if(!response){return};
            if(response.status === "success"){

                let columns 
                columns = [
                    {
                        Header : "Acc Prvdr Code",
                        id: 'acc_prvdr_code', 
                        accessor: 'acc_prvdr_code' ,
                    },
                    {
                        Header : "FA ID",
                        accessor: 'loan_doc_id' ,
                        Cell: e =>  <Link to={"/fa/view/"+e.value} target="_blank" >{e.value}</Link>,
                    
                    },
                    {
                        Header : "Account Number",
                        accessor: 'acc_number' ,
                        Cell: e =>  <Link to={"/borrower/indiv/view/"+e.value} target="_blank" >{e.value}</Link>,
                    
                    },
                    {
                        Header : "Write-Off Status",
                        id: 'write_off_status', 
                        accessor: 'write_off_status',
                    },
                    {
                        Header : "Write-Off Amount",
                        id: 'write_off_amount', 
                        accessor:row => lbl_amt(row.write_off_amount,this.currency_code),               // format_date_tbl(row.lead_date),
                    },
                    
                    {
                        Header : "Requested By",
                        id: 'req_name', 
                        accessor: 'req_name' ,
                    },
                    {
                        Header : "Requested Date",
                        id: 'req_date', 
                        accessor: 'req_date' ,
                    }
                
                ]

                this.setState({columns,lists:response.data})
                }
        });

    }

    render(){
        return (
            <div>
                <div className="card">
                    <div className="card-header card-header-icon card-header-primary">
                        <h4 className="card-title text-white d-inline-block">Write-Off Pending Approval</h4>
                    </div>
                    <div className="card-body">
                        <div className="table-responsive">
                            <ReactTable  minRows = {3} showPaginationBottom={false}  data={this.state.lists}  columns={this.state.columns}  defaultSorted={[{id: "req_date",
                    desc: true}]}  />
                        </div>
                    </div>
                </div>
            </div>
        );

}


}
export default WriteOffList;