import React,{useState,useEffect} from "react"
import { dd_value } from "../actions/common_actions"

export default function AccPurposeMultiSelect ({id,acc_purposes, initialAct_purpose, must, onChange}) {      

    const [accPur,setaccPur] = useState([])    

    useEffect(() => {
        if(initialAct_purpose){       
            setaccPur(initialAct_purpose)
        }
    },[initialAct_purpose])    

    const [dropShow, setdropShow] = useState(false)       

    const clickHandle = () => {        
        setdropShow(!dropShow)
    }                  

    const onChangehandler = (key,val) => {                                                 
        if(key){            
            if(val !== accPur[0]){
                accPur.push(val)
                setaccPur([...accPur])               
            }                        
        }else if(!key){
            var index = accPur.indexOf(val)                    
            accPur.splice(index,1)
            setaccPur([...accPur])           
        }      
        let event = {target: {id: id, value: accPur}}
        onChange(event)                 
    }    
   
    return(        
        <div className={`accPur_box ${dropShow ? "accPur_dropbox" : ''}`}>                        
            <span>Account Purpose</span>{must && <font className="text-danger">&nbsp;* </font>}                                                             
            <div type="text" className='form-control'>            
            {accPur.length === 0 ?
                <div style={{display:'inline-flex'}}>
                    <span>choose</span>                     
                </div>
            :        
                accPur.map((item,index) =>            
                    <div key={index} style={{display:'inline-flex'}}>
                        <span>{dd_value(item,"account_purpose")}</span> 
                        <i class="fa-solid fa-xmark accPur_close text-danger" onClick={()=>onChangehandler(false,item)}></i>
                    </div>
                ) 
            }            
               
            </div>           
            <i class="fa-solid fa-angle-down text-white dropdownAccpur_Pos" onClick={()=>clickHandle()} style={{cursor:"pointer"}}></i>
            {dropShow &&
                <div className='drop_style' onMouseLeave={()=>setdropShow(false)}>
                    {acc_purposes.map((each,idx)=>{
                        return each.props.children !== "choose" &&
                            <div key={idx} className="checkAndVal">                            
                                <input type="checkbox" checked={accPur.includes(each.props.value) ? true : false} onChange={(e)=>onChangehandler(e.target.checked,each.props.value)}/>                            
                                <p className="no-margin text-white" style={{cursor:'pointer',width:200}} onClick={()=>onChangehandler(accPur.includes(each.props.value) ? false : true,each.props.value)}>{dd_value(each.props.children)}</p>
                            </div>                                           
                    })}
                </div>
            }
        </div>
    )
}