import FlowComponent from '../common/component/core/FlowComponent';
import React from 'react';
import {borrowerSearch} from '../../actions/borrower_actions';
import ReactTable from "react-table";
import {format_date_tbl, capitalize_first_letter, get_time_diff, lbl_amt, Homeacc_prvdrLogoselector} from '../../helpers/common_helper';
import { Link} from 'react-router-dom';
import {searchLoan} from "../../actions/loan_actions";
import moment from "moment";
import {searchLoanAppl} from "../../actions/loan_appl_actions";
import {ExcesstimeAction, TooltipLabel} from "../common/partial";
import {FcRefresh} from "react-icons/all";
import {GrRefresh} from "@react-icons/all-files/gr/GrRefresh";
import { each } from 'jquery';
import { listAccountProvider } from '../../actions/account_provider_actions';
import { get } from '../../helpers/storage_helper';

class DisplayList extends FlowComponent {

	state = {
		columns : [],
		lists : [],	
		copy: [],	
		refresh: false,							
		dyn_Btn_selected:[],
		all_count:0,				
		filter_actions:{},
		all_Btn_status:false,
		all_Acc_pvdrs:[],				 					
	}			

	componentWillMount() {		
		this.getListData()	
		const ls_market = get('market')
		var req = {country_code: ls_market.country_code, time_zone: ls_market.time_zone, status: ""};		
		listAccountProvider({...req, status: 'enabled', biz_account: true})
		.then((response)=>{					
			if(!response){return };
			if(response.status === "success"){
				let acc_prvdrs = []            
				response.data.list.map(each=>
				acc_prvdrs.push(each.acc_prvdr_code)
				)  
				this.setState(state=>state.all_Acc_pvdrs = acc_prvdrs)  				          
			}
		});			
    }


	getListData = () => {	
		const market = get('market')
		const market_name = market.country_code			
		this.server_time(this.time_zone)			
		this.getListRows().then((lists) => {												
			let columns = this.getListColumns()						
			this.setState({lists,columns,refresh: false})					
			this.setState(state=>state.all_count = lists.length)	
			if(this.props.list_name === "pending_w_cust"){
				if(lists.length !== 0){																	
					const newArr = get([market_name]+'_pendingCUS') === undefined ?  this.state.all_Acc_pvdrs : get([market_name]+'_pendingCUS') 																						
					this.state.all_Acc_pvdrs.map(each=>{																				
						const count = lists.filter(indiv => indiv.acc_prvdr_code === each)		
						const length = count.length										
						this.setState(state=>state.filter_actions[each] = [{count:0},{status:newArr.includes(each)}])
						this.CUSdynClickhandler(this.state.filter_actions[each][1].status,each)									
						this.setState(state=>state.filter_actions[each][0].count = length)																
					})
				}
			}
			else if(this.props.list_name === "pending_w_rm"){
				if(lists.length !== 0){												
					const newArr = get([market_name]+'_pendingRM') === undefined ?  this.state.all_Acc_pvdrs : get([market_name]+'_pendingRM')																						
					this.state.all_Acc_pvdrs.map(each=>{																				
						const count = lists.filter(indiv => indiv.acc_prvdr_code === each)		
						const length = count.length										
						this.setState(state=>state.filter_actions[each] = [{count:0},{status:newArr.includes(each)}])
						this.RMdynClickhandler(this.state.filter_actions[each][1].status,each)									
						this.setState(state=>state.filter_actions[each][0].count = length)											
					})
				}																																								
			}				
		})											
	}
	
	getListRows = async () => {
		let resp
		await this.server_time(this.time_zone)		
		switch(this.props.list_name){
			case 'pending_w_cust':
				let loan_search_criteria = {pending_w_cust: 'true', mode : 'search', alert: false}
				resp = await searchLoan(this.req({loan_search_criteria}))				
				if(resp && resp.status === 'success'){ return resp.data.results}							
				break;

			case 'pending_w_rm':
				let loan_appl_search_criteria = {pending_w_rm: 'true'}
      			resp = await searchLoanAppl(this.req({loan_appl_search_criteria}))				
				if(resp && resp.status === 'success'){return resp.data}											
		}		
	}

	RMdynClickhandler (key,val) {
		const market = get('market')
		const market_name = market.country_code												
		if(key){												
			if(val !== "ALL"){								
				const find_val = this.state.dyn_Btn_selected.findIndex(each=> each === val)
				if(find_val == -1) {					
					this.state.dyn_Btn_selected.push(val)					
					this.setState(state=>state.filter_actions[val][1].status = key)
					if(this.state.dyn_Btn_selected.length === this.state.all_Acc_pvdrs.length) {												
						this.setState(state=>state.all_Btn_status = true)
					}
				}											
				localStorage.setItem([market_name]+'_pendingRM',JSON.stringify(this.state.dyn_Btn_selected))															
			}else{					
				const newOne = []
				this.state.all_Acc_pvdrs.map(e=>{
					this.state.filter_actions[e][1].status = true
					newOne.push(e)
				})					
				this.setState(state=>state.dyn_Btn_selected = newOne)
				const market_name = market.country_code								
				localStorage.setItem([market_name]+'_pendingRM',JSON.stringify(newOne))																						
				this.setState(state=>state.all_Btn_status=key)				
			}					
		}		
		else if(!key){					
			if(val !== "ALL"){											
				const find_val = this.state.dyn_Btn_selected.findIndex(each=>each === val)							
				if(find_val > -1){					
					this.state.dyn_Btn_selected.splice(find_val,1)
					this.setState(state=>state.filter_actions[val][1].status = key)
				}							
				this.setState(state=>state.all_Btn_status=false)													
				localStorage.setItem([market_name]+'_pendingRM',JSON.stringify(this.state.dyn_Btn_selected))									
			}else{				
				this.state.all_Acc_pvdrs.map(e=>
					this.state.filter_actions[e][1].status = false
				)				
				this.setState(state=>state.dyn_Btn_selected = [])								
				localStorage.setItem([market_name]+'_pendingRM',JSON.stringify([]))
				this.setState(state=>state.all_Btn_status=key)
			}
		}			
		const newArr = get([market_name]+'_pendingRM')
		let combinedArr = []
		newArr.map(each=> {			
			const filterArr = this.state.lists.filter(indiv=>indiv.acc_prvdr_code === each)
			combinedArr.push(...filterArr)			
		})		
		this.setState(state=>state.copy = combinedArr)									
	}

	CUSdynClickhandler (key,val) {
		const market = get('market')
		const market_name = market.country_code									
		if(key){												
			if(val !== "ALL"){								
				const find_val = this.state.dyn_Btn_selected.findIndex(each=> each === val)
				if(find_val == -1) {					
					this.state.dyn_Btn_selected.push(val)					
					this.setState(state=>state.filter_actions[val][1].status = key)
					if(this.state.dyn_Btn_selected.length === this.state.all_Acc_pvdrs.length) {												
						this.setState(state=>state.all_Btn_status = true)
					}
				}				
				localStorage.setItem([market_name]+'_pendingCUS',JSON.stringify(this.state.dyn_Btn_selected))											
			}else{					
				const newOne = []
				this.state.all_Acc_pvdrs.map(e=>{
					this.state.filter_actions[e][1].status = true
					newOne.push(e)
				})					
				this.setState(state=>state.dyn_Btn_selected = newOne)																												
				localStorage.setItem([market_name]+'_pendingCUS',JSON.stringify(newOne))
				this.setState(state=>state.all_Btn_status=key)				
			}					
		}		
		else if(!key){					
			if(val !== "ALL"){											
				const find_val = this.state.dyn_Btn_selected.findIndex(each=>each === val)							
				if(find_val > -1){					
					this.state.dyn_Btn_selected.splice(find_val,1)
					this.setState(state=>state.filter_actions[val][1].status = key)
				}							
				this.setState(state=>state.all_Btn_status=false)													
				localStorage.setItem([market_name]+'_pendingCUS',JSON.stringify(this.state.dyn_Btn_selected))
												
			}else{				
				this.state.all_Acc_pvdrs.map(e=>
					this.state.filter_actions[e][1].status = false
				)				
				this.setState(state=>state.dyn_Btn_selected = [])								
				localStorage.setItem([market_name]+'_pendingCUS',JSON.stringify([]))
				this.setState(state=>state.all_Btn_status=key)
			}
		}			
		const newArr = get([market_name]+'_pendingCUS')
		let combinedArr = []
		newArr.map(each=> {			
			const filterArr = this.state.lists.filter(indiv=>indiv.acc_prvdr_code === each)
			combinedArr.push(...filterArr)			
		})		
		this.setState(state=>state.copy = combinedArr)									
	}					

	getListColumns = () => {								
		switch(this.props.list_name){			
			case 'pending_w_cust':				
				return [
					{
						Header : "ACC PRVDR",
						id: 'acc_prvdr_code' ,
						accessor:row => <Homeacc_prvdrLogoselector logotype={row.acc_prvdr_code}/>
					},
					{
						Header: 'FA ID',
						id: 'loan_doc_id',
						accessor: row => <Link to={"fa/view/"+row.loan_doc_id} target="_blank" >{row.loan_doc_id}</Link>
					},
					{
						Header : "A/C Number",
						id: 'acc_number',
						accessor: row =>  <Link to={"/borrower/indiv/view/"+row.cust_id} target="_blank" >{row.acc_number}</Link>,

					},
					{
						Header : "Mobile Number",
						id: 'cust_mobile_num',
						accessor: row => {return row.cust_mobile_num}
					},
					{
						Header : "Biz Name",
						id: 'biz_name',
						accessor: row => <TooltipLabel full_text={row.biz_name} overlay={row.cust_name} no_reduce={true} />
					},{
						Header : ()=>(<div className={'tableAmount_centerAlign'}>FA Amount</div>),
						id: 'amount',		        			
						accessor: row => <div className={'tableAmount_rightAlign'}>{lbl_amt(row.loan_principal,this.currency_code)}</div>
					},
					{
						Header : "RM Approved",
						id: 'date',
						accessor:row => {let appr_date = (row.loan_approved_date == null) ? row.loan_appl_date : row.loan_approved_date											
											return get_time_diff(this.state.server_time.server_time_db, appr_date)
										}
					},
					{
						Header : "No. of Conf. Codes Sent",
						accessor: 'otp_count',
					}]
			case 'pending_w_rm':							
				return [
					{
						Header : "ACC PRVDR",
						id: 'acc_prvdr_code' ,
						accessor:row => <Homeacc_prvdrLogoselector logotype={row.acc_prvdr_code}/>
					},
					{
						Header: 'FA Application ID',
						id: 'loan_appl_doc_id',
						accessor: row => <Link to={"/fa_appl/view/"+row.loan_appl_doc_id} target="_blank">{row.loan_appl_doc_id}</Link>
					},
					{
						Header : "A/C Number",
						id: 'acc_number',
						accessor: row =>  <Link to={"/borrower/indiv/view/"+row.cust_id} target="_blank" >{row.acc_number}</Link>,
					},
					{
						Header : "Mobile Number",
						id: 'cust_mobile_num',
						accessor: row => {return row.cust_mobile_num}
					},
					{
						Header : "Biz Name",
						id: 'biz_name',
						accessor: row => <TooltipLabel full_text={row.biz_name} overlay={row.cust_name} no_reduce={true} />
					},
					{
						Header : ()=>(<div className={'tableAmount_centerAlign'}>FA Amount</div>),							
						id: 'amount',
						accessor: row => <div className={'tableAmount_rightAlign'}>{lbl_amt(row.loan_principal,this.currency_code)}</div>
					},
					{
						Header : "Applied",
						id: 'date',		        			
						accessor:row => <ExcesstimeAction data={row} timedata={this.state.server_time.server_time_db} item={"applied"}/>
					},
					{
						Header : "Next Action",
						id: 'loan_approver_name' ,							
						accessor: row => <ExcesstimeAction data={row} timedata={this.state.server_time.server_time_db} item={"next_action"}/>
					}]						
		}		
	}
	
	refreshList = () => {		
		this.setState({refresh: true})
		this.getListData()			
	}

	componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.refresh != this.props.refresh){
            this.refreshList()
        }		
    }

    render(){
		const NullComponent = () => null;
        return (			
				<div>
					<div className="card">
						<div className="card-header card-header-icon card-header-primary d-flex">					
							{this.props.list_name == "pending_w_cust" &&
								<div style={{display:'inline-flex'}}>
									<h4 className="card-title text-white tableHead_font no-margin">{this.getListTitle()}</h4>
									{this.state.lists.length > 0 &&
										<>
											<div className='countREL'>
												<button className={'filterBtns allBtn_size'} onClick={()=>this.CUSdynClickhandler(!this.state.all_Btn_status,"ALL")} style={{filter: !this.state.all_Btn_status ? "opacity(0.5)" : ''}}>ALL</button>
												<div className='countABS' style={{backgroundColor : !this.state.all_Btn_status ? "lightgrey" : ''}}>
													<label>{this.state.all_count}</label>
												</div>
											</div>									
											{this.state.all_Acc_pvdrs.map((each,idx)=>									
												<div key={idx} className='countREL'>																					
													<>
														<div style={{width:60,height:30}} className="filterBtns"  onClick={()=>this.CUSdynClickhandler(!this.state.filter_actions[each][1].status,each)}>													  
															<img className='img-fluid' src={`/img/${each}_logo_color.png`} style={{filter: this.state.filter_actions[each] && !this.state.filter_actions[each][1].status ? "opacity(0.5)" : ''}}></img> 																	
														</div>
														<div className='countABS' style={{backgroundColor : this.state.filter_actions[each] && !this.state.filter_actions[each][1].status ? "lightgrey" : ''}}>													
															<label>{this.state.filter_actions[each] && this.state.filter_actions[each][0].count}</label>																																
														</div>
													</>										
												</div>																		
											)}
										</>
									}
								</div>							
							}
							{this.props.list_name === "pending_w_rm" &&							
								<div style={{display:'inline-flex'}}>
									<h4 className="card-title text-white tableHead_font no-margin">{this.getListTitle()}</h4>
									{this.state.lists.length > 0 && 
										<>
											<div className='countREL'>
												<button className={'filterBtns allBtn_size'} onClick={()=>this.RMdynClickhandler(!this.state.all_Btn_status,"ALL")} style={{filter: !this.state.all_Btn_status ? "opacity(0.5)" : ''}}>ALL</button>
												<div className='countABS' style={{backgroundColor : !this.state.all_Btn_status ? "lightgrey" : ''}}>
													<label>{this.state.all_count}</label>
												</div>
											</div>									
											{this.state.all_Acc_pvdrs.map((each,idx)=>									
												<div key={idx} className='countREL'>																					
														<>
															<div style={{width:60,height:30}} className="filterBtns"  onClick={()=>this.RMdynClickhandler(!this.state.filter_actions[each][1].status,each)}>													  
																<img className='img-fluid' src={`/img/${each}_logo_color.png`} style={{filter: this.state.filter_actions[each] && !this.state.filter_actions[each][1].status ? "opacity(0.5)" : ''}}></img> 																	
															</div>
															<div className='countABS' style={{backgroundColor : this.state.filter_actions[each] && !this.state.filter_actions[each][1].status ? "lightgrey" : ''}}>													
																<label>{this.state.filter_actions[each] && this.state.filter_actions[each][0].count}</label>																																
															</div>
														</>										
												</div>
																														
											)}
										</>
									}
								</div>
							}														
						</div>																
						<div className="card-body">							
							<div className="table-responsive">
								<ReactTable NoDataComponent={NullComponent} showPaginationBottom={false} minRows = {1}  data={this.state.copy}  
								columns={this.state.columns} 
								resizable={false}  defaultSorted={[{id: "acc_prvdr_code",
								desc: true}]}  />
							</div>							
						</div>
					</div>
				</div>
        	)		
    }

	getListTitle = () => {
		switch (this.props.list_name){
			case 'pending_w_cust':
				return "FAs Pending Customer Confirmation"
			case 'pending_w_rm':
				return "FAs Pending RM Approval"
		}
	}
}


export default DisplayList;
